



































import Vue from 'vue';
import { isLoggedIn, login } from '@/services/auth';

export default Vue.extend({
	name: 'Login',

	data: () => ({
		username: '',
		password: '',
		state: 'idle'
	}),

	methods: {
		handleLogin() {
			const payload = { username: this.username, password: this.password };
			const result = login(payload);
			if (!result) {
				this.state = 'fail';
			} else {
				this.$router.replace('/app/balita/list');
			}
		}
	},

	mounted() {
		if (isLoggedIn()) {
			this.$router.replace('/app/balita/list');
		}
	}
});
